import Container from "../components/container"
import * as React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import Header from "../components/header"
import Footer from "../components/footer"

export default function Diary({data}) {
  return (
    <Container>
      <Header headerTitle="collections"></Header>
      <section className="hero">
        <div className="hero-body">
          <p className="title">
            #diary
          </p>
          <p className="subtitle">
            of adventures, a pic for every unforgettable moment
          </p>
        </div>
      </section>
      <section className="section">
        <div className="content diary-content" dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }}></div>
      </section>
      <Footer></Footer>
    </Container>
  )
}

export const query = graphql`
  query {
    markdownRemark(fields: { slug: { eq: "/diary-page/" } }) {
      html
    }
  }
`
